import React, { useState } from 'react';
import styled from "styled-components"
const TextBox = (props) => {
    // 3 항 연산자, 속성으로 안오면 default 값
    const width = (props.width !== undefined)? props.width: "49%"; 
    const height = (props.height !== undefined)? props.height: "100%";
    const fontSize = (props.fontSize !== undefined)? props.fontSize : "20px";
    const fontColor = (props.fontColor !== undefined)? props.fontColor : "white"
    const backgroundColor = props.backgroundColor
    const CustomStyle = styled.div`
        display: flex;
        color: ${fontColor};
        font-size: ${fontSize};
        width: ${width};
        height: ${height};
        text-align: center;
        justify-content: center;
        align-items: center;
        white-space: pre-line;
        @media only screen and (max-width: 768px) {
            width: 100%;
          }
        overflow: hidden;
        background-color: ${backgroundColor};
    `;

    return(
          <CustomStyle>
              <text width="100%">
              {props.text}
              </text>
          </CustomStyle>
    )
  }


  export default TextBox;