import React, { useState } from 'react';
import styled from "styled-components"

const ImageBox = (props) => {
  const img = (props.img !== undefined)? props.img : "";
  const width = (props.width !== undefined)? props.width: "49%"; 
  const height = (props.height !== undefined)? props.height: "100%";
  const fontSize = (props.fontSize !== undefined)? props.fontSize : "20px";
  const fontColor = (props.fontColor !== undefined)? props.fontColor : "white"
  const link = (props.link !== undefined)? props.link : null
  
  const onClick = (e) => {
    e.preventDefault();
    if(props.link === undefined) return
    window.open(props.link)
  };
  const CustomStyle = styled.div`
  justify-content: center;
  align-items: center;
  color: ${fontColor};
      font-size: ${fontSize};
      width: ${width};
      height: ${height};
      text-align: center;
      inner-padding: 10px;
      white-space:normal;
        @media only screen and (max-width: 768px) {
    width: 100%;
  }
      margin-bottom: 20px;
  `;

  const Title =styled.text`
  color: white;
  font-size: 20px;
  `

    return(
      <CustomStyle id="ImageBox">        
          <img src={img} width = "100%" height = "100%" onClick = {onClick}/>
          { props.title !== undefined &&
          <Title>{props.title}</Title>
          }
      </CustomStyle>
    )
  }

  export default ImageBox;